.App{
    height: 100%;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    display: block;
    
    
    
}
.noSelect {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
#title{
    text-align: center;
    padding: 60px;
    padding-bottom: 25px;
    font-size: 45px;
    
    color: white;
}
#container1{
    display: flex;
    justify-content: center;
    align-items: center;
}
#container2{
    display: flex;
    justify-content: center;
    align-items: center;
}
div{

    height: 33%;
}
@media only screen and (max-width: 600px){
    #container1{
        margin-bottom: 45px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    #title{
        padding: 25px;
    }
}
