#break-div{
    margin: 15px;
    width: 25%;
    display: flex;
    align-items: flex-start;
    flex-flow: wrap;
    justify-content: center;
}
#break-label{
    width: 100%;
    padding-bottom: 10px;
    font-size: 30px
}
#break-container{
    display: flex;
    align-items: center;
    justify-content: center;
}
#break-length{
    margin-top: 15px;
    padding: 35px;
    font-size: 30px;
    color: white;
}
@media only screen and (max-width: 600px){
    #break-length{
        margin-top: 5px;
    }
}